export default function InfoBox({ status }) {
  return (
    <>
      {status.Status ? (
        <p> : {status.Status}</p>
      ) : (
        <p> :  </p>
      )}
      {status.WarehouseSender ? (
        <p>
          : <>{status.WarehouseSender}</>
        </p>
      ) : (
        <p>:  </p>
      )}
      {status.DateCreated ? (
        <p>
           :<>{status.DateCreated}</>
        </p>
      ) : (
        <p> :  </p>
      )}
      {status.WarehouseRecipient ? (
        <p>
          : <>{status.WarehouseRecipient}</>
        </p>
      ) : (
        <p>:  </p>
      )}
      {status.RecipientDateTime ? (
        <p>
           :<>{status.RecipientDateTime}</>
        </p>
      ) : (
        <p> :  </p>
      )}
    </>
  );
}
