import { nanoid } from '@reduxjs/toolkit';

export default function HistoryBox({ historyList, fetchInfo, clearList }) {
  return (
    <>
      <></>
      <ul>
        {historyList.map(item => {
          const id = nanoid();
          return (
            <li key={id}>
              <button onClick={() => fetchInfo(item)}>{item}</button>
            </li>
          );
        })}
        <button onClick={clearList}></button>
      </ul>
    </>
  );
}
